@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.css);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,300,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --off-white: #333;
}

.App {
  font-family: "Raleway", sans-serif;
  margin: 0;
  padding: 0;
}

/**
* INDEX:
*   1-HEADER
    2-ABOUT
    3-PROJECTS
    4-CONTACT
    5-FOOTER

*/

/*
  1 HEADER 
*/

.header {
  background-image: linear-gradient(135deg, rgba(6, 9, 9, 0.7) 0%, rgba(6, 9, 9, 0.7) 100%), url(/portfolio/static/media/home_2.cd54b4f7.jpg);
  background-position: center;

  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;

  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
}

.welcome {
  margin-top: 30vh;
  margin-left: 10vw;
  font-size: 30px;
  letter-spacing: 8px;
  color: white;
  width: 50%;
  line-height: 90px;
}

.welcome-responsive {
  margin-left: 10vw;
  font-size: 30px;
  letter-spacing: 8px;
  color: white;
  width: 50%;
}

.social-media {
  font-size: 12px;
  text-transform: uppercase;
}

.social-media a {
  text-decoration: none;
  color: wheat;
}

.social-media a:hover {
  color: whitesmoke;
}

@media only screen and (max-width: 1100px) and (min-width: 767px) {
  .welcome {
    margin-top: 30vh;
    margin-left: 10vw;
    font-size: 25px;
    letter-spacing: 8px;
    color: white;
    width: 60%;
    line-height: 60px;
  }

  .welcome-responsive {
    margin-top: 30vh;
    margin-left: 10vw;
    font-size: 25px;
    letter-spacing: 8px;
    color: white;
    width: 60%;
    line-height: 60px;
  }
  .social-media a {
    display: block;
  }
}

@media only screen and (max-width: 767px) {
  .welcome {
    margin: 10vh 10vw;
    font-size: 20px;
    width: -webkit-fill-available;
    width: -moz-available;
    width: stretch;
    text-align: center;
    line-height: 30px;
  }
  .welcome-responsive {
    display: none;
  }

  .social-media {
    margin-top: 10vh;
  }
  .social-media a {
    display: block;
  }
}

@media only screen and (max-width: 1100px) and (min-width: 767px) and (max-height: 600px) {
  .welcome {
    margin-top: 30vh;
    margin-left: 10vw;
    font-size: 16px;
    letter-spacing: 4px;
    color: white;
    width: 60%;
    line-height: 45px;
  }

  .welcome-responsive {
    margin-top: 30vh;
    margin-left: 10vw;
    font-size: 16px;
    letter-spacing: 4px;
    color: white;
    width: 60%;
    line-height: 45px;
  }
}
/*
  2 ABOUT 
*/

.about-content {
  margin: 0;
  padding: 0;
}

.about-row {
  float: left;
}

.about-col-left {
  float: left;
  width: 50vw;
}

.about-col-right {
  margin-left: 5vw;
  margin-right: 0vw;
  margin-top: 0;
  float: left;
  width: 40vw;
  z-index: 100;
  text-align: center;
}

.title {
  font-size: 35px;
  margin: 10vh 5vw;
  color: #ff8c00;
}

.about-description {
  margin-left: 10vw;
  margin-top: 50px;

  text-align: justify;
  text-justify: inter-word;
  line-height: 50px;
}

.about-description p {
  margin-bottom: 75px;
  font-size: 22px;
}

.button-right {
  margin-left: 30vw;
}

.about-foto {
  margin-top: 20vh;
  padding-bottom: 20vh;
  width: calc(50%);
  height: auto;
  z-index: -100;
}

.skills-content {
  margin-left: 10vw;
  margin-right: 5vw;
  margin-top: 50px;
  margin-bottom: 10vh;
}

.skills-content h2 {
  font-size: 35px;
  margin-bottom: 30px;
}

.skills-lane {
  background: #ff8c00;
  margin-left: 50px;
  margin-bottom: 100px;
  width: 100px;
  height: 5px;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}
.column-skills {
  float: left;
  width: 33%;
  text-align: center;
}

.column-skills h3 {
  font-size: 30px;
}

.column-skills p {
  font-size: 20px;
}

.date-career {
  color: grey;
  font-weight: thin;
}

@media only screen and (max-width: 767px) {
  .column-skills {
    width: 100%;
  }
  .about-col-left {
    float: left;
    width: 90%;
  }

  .about-col-right {
    display: none;
  }

  .about-description p {
    font-size: 15px;
  }
}

@media only screen and (max-width: 1100px) and (min-width: 767px) {
  .column-skills {
    width: 50%;
  }
  .about-col-left {
    float: left;
    width: 90%;
  }

  .about-col-right {
    float: left;
    width: 90%;
  }

  .about-description p {
    font-size: 15px;
  }
  .title {
    font-size: 35px;
    margin: 0 5vw;
    color: #ff8c00;
  }
}
/* 
  PROJECTS 
*/

.projects-content {
  background: #3d9970;
  height: auto;
  width: auto;
  margin: 0;
  padding-bottom: 10vh;
}

.projects-content h1 {
  padding-top: 10vh;
  margin-left: 5vw;
  color: white;
  font-size: 35px;
}

.projects-container {
  height: auto;
}

.loading {
  text-align: center;
  padding-top: 10vh;
  margin-left: 5vw;
  color: white;
  font-size: 105px;
  font-weight: bold;
}

.loading:after {
  content: "...";
  display: inline-block;
  vertical-align: bottom;
  animation: ellipsis-dot 1s infinite 0.3s;
  -webkit-animation: ellipsis-dot 1s infinite 0.3s;
  /*animation-fill-mode: fowards;*/
  width: 1.25em;
}

@media only screen and (max-width: 1100px) and (min-width: 767px) {
  .loading {
    font-size: 80px;
  }
}

@media only screen and (max-width: 767px) {
  .projects-content h1 {
    font-size: 35px;
  }

  .loading {
    font-size: 40px;
  }
}

@keyframes ellipsis-dot {
  25% {
    content: "";
  }
  50% {
    content: ".";
  }
  75% {
    content: "..";
  }
  100% {
    content: "...";
  }
}

@-webkit-keyframes ellipsis-dot {
  25% {
    content: "";
  }
  50% {
    content: ".";
  }
  75% {
    content: "..";
  }
  100% {
    content: "...";
  }
}

/* 
  CONTACT 
*/

.contact-content {
  background: #0b0a15;
  margin: 0;
  padding: 10;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  height: auto;
  width: auto;
}

.contact-col-left {
  padding-top: 15vh;
  margin-left: 5vw;
  margin-right: 5vw;
  float: left;
  width: 40%;
}

.contact-col-right {
  margin-top: 15vh;
  margin-left: 10vw;
  float: left;
  width: auto;
}

.contact-title {
  position: relative;
  color: #ff8c00;
  font-size: 35px;
}

.contact-phrase {
  color: white;
  font-size: 50px;
}

.contact-email {
  text-decoration: none;
  color: white;
  font-size: 15px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 8px;
}

.contact-email:hover {
  color: black;
}

.contact-button {
  display: table-cell;
  vertical-align: middle;
  width: 40%;
  height: 50px;
  text-align: center;
  background-color: #ff8c00;
  margin-top: 100px;
  margin-left: 100px;
}

.contact-button:hover {
  background-color: white;
}

.contact-social h1 {
  font-size: 25px;
  margin-bottom: 50px;
  color: #51505b;
}

.contact-social h2 {
  color: white;
  font-weight: 500;
  font-size: 30px;
}

.contact-social a {
  text-decoration: none;
  color: white;
}

.contact-social a:hover {
  color: #ff8c00;
}

.contact-content h3 {
  color: white;
}

@media only screen and (max-width: 1100px) {
  .contact-col-left {
    width: 90%;
  }
  .contact-col-right {
    text-align: center;
    margin-left: 5vw;
    margin-right: 5vw;
    margin-bottom: 5vh;
    width: 90%;
  }

  .contact-phrase {
    color: white;
    font-size: 40px;
  }

  .contact-social h1 {
    font-size: 15px;
  }

  .contact-social h2 {
    font-size: 20px;
  }
}

/*
  FOOTER
*/

.footer {
  margin: 0;
  padding: 5vh 5vw;
  height: auto;
  background: #0b0a15;
}

.footer h3 {
  margin-top: 0;
  padding: 0;
  color: #51505b;
  font-size: 15px;
}

.footer a {
  margin-top: 100px;
  color: #51505b;
  text-decoration: none;
}

.footer a:hover {
  color: #ff8c00;
}

/******************/

.section {
}

.section-dark {
  background: #333;
  color: white;
}

.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}

.button-link {
  position: -webkit-sticky;
  position: sticky;
  margin-top: 5vh;
  top: 10vh;
  left: 90vw;
  width: 60px;
  height: 60px;
}
.bg,
.button {
  width: 50px;
  height: 50px;
  border-radius: 100%;
}

.container {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.bg {
  -webkit-animation: pulse 1.2s ease infinite;
          animation: pulse 1.2s ease infinite;
  background: #ff8c00;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 99;
  border: none;
  background: #ff8c00;
  background-size: 18px;
  cursor: pointer;
  outline: none;
  color: #fff;
}

@-webkit-keyframes pulse {
  0% {
    transform: scale(1, 1);
  }
  50% {
    opacity: 0.3;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1, 1);
  }
  50% {
    opacity: 0.3;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}

